<template>
    <div class="Login">
        <div class="alert alert-danger" v-if="LogoutE == true" role="alert">
            {{ LogoutEM }}
        </div>

        <h1>Logout</h1>
        <button class="btn btn-danger" @click="doLogout">Logout</button>
    </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
export default {
  name: 'Logout',
  data(){
    return{
        LogoutE: false,
        LogoutEM: ""
    }
  },
  methods:{
    doLogout(){
        axios
          .post('/api/authtoken/logout/',{
          password: this.password,
          username: this.username,
          })
          .then(response => {
            this.$store.commit('logout')
            this.$router.push("/login")
          })
          .catch(error => {
            this.LogoutE = true
            this.LogoutEM = error.response.data.detail
          })        
    }
  }
}
</script>
