import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/HomeView.vue'
import Detail from '../views/Detail.vue'
import AboutView from '../views/AboutView.vue'
import Profile from '../views/Profile.vue'
import Login from '../views/Login.vue'
import Logout from '../views/Logout.vue'
import store from '@/store'
import Register from '../views/Register.vue'
import Add from '../views/Add.vue'


const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    meta:{
      title:"Home"
    }
  },
  {
    path: '/article/:slug',
    name: 'detail',
    component: Detail
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView,
    meta:{
      title:"About"
    }
  },
  {
    path: '/add',
    name: 'add',
    component: Add,
    meta:{
      title:"Add"
    }
  },
  {
    path:'/profile',
    name:'profile',
    component: Profile,
    meta: { loginRequired: true,title:"profile" },

  },
  {
    path:'/login',
    name:'Login',
    component: Login,
    meta: { loginRedirect: true,title:"Login" },
  },
  {
    path:'/logout',
    name:'Logout',
    component: Logout,
    meta: { loginRequired: true,title:"Logout" },
  },
  {
    path:'/register',
    name:'register',
    component: Register,
    meta: { loginRedirect: true,title:"Register" },
    
  },

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})
router.beforeEach((to, from) => {
  document.title = to.meta?.title ?? "App"
  if (to.meta.loginRequired && !store.state.isAuthenticated) {
   
    return {
      path: '/login',
      query: { redirect: to.fullPath },
    }

  }
  else if (to.meta.loginRedirect && store.state.isAuthenticated) {
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    return {
      path: '/profile',
      // save the location we were at to come back later
      query: { redirect: to.fullPath },
    }
  }


})
export default router
