<template>
    <div class="Detail">
      <div v-if="!articleNotFound">
        <article>
      <h1>{{ article.title }}</h1>
      <div>{{ article.content }}</div>
    </article>
        <hr>
    <div v-if="$store.state.isAuthenticated && !articleNotFound">
      <button class="btn btn-warning mr-1" @click="edit=!edit">Edit</button>
      <button class="btn btn-danger" @click="doRemove">Remove</button>
    </div>

    </div>
    <div class="alert alert-warning" v-if="articleNotFound">Article Not Found</div>

    <div class="alert alert-danger" v-if="errorDelete">Something went wrong.</div>


    <!-- form edit -->
    <form @submit.prevent="doEdit" v-if="edit">
  <div class="alert alert-danger" v-if="errorEdit">Something went wrong.</div>
  <div class="mb-3">
  <label for="TitleInput" class="form-label">Title</label>
  <input class="form-control" id="TitleInput" v-model="title">
</div>
<div class="mb-3">
  <label for="DescriptionInput" class="form-label">Description</label>
  <textarea class="form-control" id="DescriptionInput" rows="3" v-model="description"></textarea>
</div>
<div class="mb-3">
  <label for="ContentInput" class="form-label">Content</label>
  <textarea class="form-control" id="ContentInput" rows="9" v-model="content"></textarea>
</div>
<button class="btn btn-primary">Edit article</button>
</form>
  </div>
  

  </template>
  
  <script>
import axios from 'axios'
  export default {
    name: 'Detail',
    data(){
      return{
        article:{},
        articleNotFound:false,
        title: "",
        description:"",
        content:"",
        edit:false,
        errorEdit:false,
        errorDelete:false,
      }
    },
    mounted(){
          axios
    .get(`/articles/${this.$route.params.slug}/`)
          .then(response => {
            this.article = response.data
            this.title= this.article.title
            this.description= this.article.description
            this.content= this.article.content
  
          })
          .catch(error => {
            this.articleNotFound = true
            console.log(error)
          })

    },
    methods:{
      doEdit(){
        let article = {
        title:this.title,
        slug:this.title.replaceAll(" ","-").toLowerCase(),
        description:this.description,
        content:this.content,
     }
      axios
    .put(`/articles/${this.$route.params.slug}/`, article)
          .then(response => {
            this.article = article
            this.edit = false
            this.$router.push(`/article/${article.slug}`)
          })
          .catch(error => {
            this.errorEdit = true
          })     
      },
    
      doRemove(){
        axios
    .delete(`/articles/${this.$route.params.slug}/`)
          .then(response => {
            this.$router.push('/')
          })
          .catch(error => {
            this.errorDelete = true
          })     
      }
    }
  }
  
  </script>
  