<template>
    
    <div class="Register">
        <div class="row">
          <div class="col-lg-4 mx-auto shadow rounded p-4 mt-3">
            <h1>Register</h1>
        <form @submit.prevent="doLogin">
      <div class="mb-3">
        <div class="alert alert-danger" v-if="usernameE == true" role="alert">
            {{ usernameEM }}
        </div>

         <label for="usernameInput" class="form-label">Username</label>
         <input type="text" class="form-control" id="usernameInput" v-model="username" :class="{'is-invalid':usernameE==true,'is-valid':usernameE===false}">
      </div>
      <div class="mb-3">
        <div class="alert alert-danger" v-if="passwordE == true" role="alert">
            {{ passwordEM }}
        </div>
        <label for="passwordInput" class="form-label">Password</label>
        <input type="password" class="form-control" id="passwordInput" v-model="password" :class="{'is-invalid':passwordE===true,'is-valid':passwordE===false}">
      </div>
      <div class="mb-3">
        <label for="passwordInput2" class="form-label">Repeat Password</label>
        <input type="password" class="form-control" id="passwordInput2" v-model="password2" :class="{'is-invalid':password2E===true,'is-valid':password2E===false}">
      </div>
      <button type="submit" class="btn btn-primary">Register</button>
      </form>
          </div>
        </div>
    </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
export default {
  name: 'Register',
  data(){
    return{
      username:'',
      password:'',
      password2:'',
      usernameE: false,
      passwordE: false,
      password2E: false,
      usernameEM: false,
      passwordEM: false,
    }
  },
  methods:{
    doLogin(){
      let access = true

        if (this.username.length < 5){
          this.usernameE = true
          access = false
          if (this.username.length == 0){
            this.usernameE = true
            this.usernameEM = 'Username rquired.'
          }
          else{
            this.usernameEM = 'Username must be at least 5'

          }
        }else{
          this.usernameE = false
          this.usernameEM = ''
        }
        if (this.password.length < 6){
          this.passwordE = true
          access = false
          if (this.password.length == 0){
            this.passwordE = true
            this.passwordEM = 'Password rquired.'
          }
          else{
            this.passwordEM = 'Password must be at least 5'

          }
        }else{
          this.passwordE = false
          this.passwordEM = ''
        }
        


        if (this.password2.length < 6){
          this.passwordE = true
          access = false
          if (this.password.length == 0){
            this.passwordE = true
            this.passwordEM = 'Password rquired.'
          }
          else{
            this.passwordEM = 'Password must be at least 5'
          }
        }else{
          this.passwordE = false
          this.passwordEM = ''
        }

        if (this.password != this.password2){
            access = false
            this.passwordE = true
            this.password2E = true
            this.passwordEM = 'Password do not match.'
        }
        if (access){
          axios
          .post('/api/authusers/',{
          password: this.password,
          username: this.username,
          })
          .then(response => {
            console.log(response)
            this.$store.commit('login',response.data.auth_token)
            this.$router.push('/profile')
          })
          .catch(error => {
            if(!error.response){
              this.usernameE = true
              this.usernameEM = "Something went wrong."

            }

            if (error.response.data.username){
              this.usernameE = true
              this.usernameEM = error.response.data.username.join(' ')

            }else if (error.response.data.password){
              this.passwordE =true
              this.passwordEM = error.response.data.password.join(' ')

            }
                        
          })

        }
    }
  }
}

</script>
