<template>
    <div class="Profile">
        <h1>Profile</h1>
        <div class="alert alert-success">Hello {{username}}</div>
    </div>
</template>


<script>
import axios from 'axios'
export default {
  name: 'Logout',
  data(){
    return{
        username: ''
    }
  },
  mounted(){
    axios
    .get('/api/authusers/me/')
          .then(response => {
            this.username = response.data.username
            console.log(response) 
          })
          .catch(error => {
            this.$store.commit('logout')
            this.$router.push('login')
          })
  }
}
</script>